/**
 * toon
 * @param {*} apiType
 * @param {*} apiName
 * @param {*} params
 * @param {*} cb
 */
function toongine (apiType, apiName, params, cb) {
    if (params && typeof (params) !== 'function') {
        window.toongine[apiType][apiName]({
            params: params,
            callback: res => {
                cb && cb(res);
            }
        });
    } else {
        window.toongine[apiType][apiName]({
            callback: res => {
                params && params(res);
            }
        });
    }
}
export default toongine;
