<template>
    <div id="app">
        <transition
            @before-enter="beforeEnter"
            @enter="enter"
            @after-enter="afterEnter"
            @leave="leave"
            :css="false"
        >
            <keep-alive>
                <router-view ref="page" v-if="$route.meta.keepAlive" v-scroll-position></router-view>
            </keep-alive>
        </transition>
        <transition
            @before-enter="beforeEnter"
            @enter="enter"
            @after-enter="afterEnter"
            @leave="leave"
            :css="false"
        >
            <router-view ref="page" v-if="!$route.meta.keepAlive" v-scroll-position></router-view>
        </transition>
    </div>
</template>

<script>
    import Vue from 'vue';
    import ScrollPosition from './vendor/v-scroll-position.js';
    import VConsole from 'vconsole';

    Vue.use(ScrollPosition); // 

    export default {
        name: 'app',
        data () {
            return {
            };
        },
        mounted () {
            this.loadingObj = document.getElementById('loading-wrapper');
        },
        watch: {
            '$store.state.status.loading' (val) {
                if (this.loadingObj && val) {
                    this.loadingObj.style.display = 'block';
                } else if (this.loadingObj && !val) {
                    this.loadingObj.style.display = 'none';
                }
            }
        },
        computed: {
        },
        methods: {
            beforeEnter () {
            },
            enter (el) {
                if (this.$router.direct() && el) { // transitiondirectiveinserted
                    // keep-alive
                    ScrollPosition.clear(el);
                } else if (!this.$router.direct() && el) { // 
                    this.$nextTick().then(() => {
                        ScrollPosition.inserted(el);
                    });
                }
            },
            afterEnter () { // 
            },
            leave () { // 
            }
        },
        created () {
            var vConsole = new VConsole();  // eslint-disable-line
        }
    };
</script>

<style rel="stylesheet/scss" lang="scss">
    @import './scss/app.scss';
    @import "./scss/variables";

    #app {
        font-family: Arial;
        -webkit-font-smoothing: antialiased;
        -moz-osx-font-smoothing: grayscale;
        width: 100%;
        height: 100vh;
    }

</style>
