let lang = function () {    // eslint-disable-line
    let arr = navigator.userAgent.split(' ');
    for (let i = 0;i < arr.length;i++) {
        if (arr[i].indexOf('language') > -1) {
            if (arr[i].split('/')[1].indexOf('zh') > -1) {
                return 'zh';
            } else if (arr[i].split('/')[1].indexOf('en') > -1) {
                return 'en';
            } else {
                return 'zh';
            }
        }
    }
};

// 
const config = {
    instance: 100000,   // 
    title: lang() === 'zh' ? '' : 'Trusted Platform',
    url: '',
    // userList: ['weihy@aaaa.com', 'weihy1@aaaa.com'],
    // userList: ['abc@qq.com', 'dbc@aa.com'],
    userList: [],
    lang: lang()  // 
    // lang: 'zh'
};
if (process.env.NODE_ENV === 'development') {   // 
    config.url = 'http://172.28.43.105:50002/bbchain';
} else if (process.env.NODE_ENV === 'production') { // .
    config.url = window.location.origin + '/bbchain';
}

export default config;
