import { publicPost } from '../../js/api/index.js';
import getApiUrl from '../../js/api/apiUrlList';
import config from '../../config.js';
const state = {
    tradeType: null, // 
    serveType: null, // 
    navList: [], // 
    searchListData: [] // 
};

const mutations = {
    // 
    setServeType (state, obj) {
        state.serveType = obj;
    },
    // 
    setTradeType (state, obj) {
        state.tradeType = obj;
    },
    // 
    setNavList (state, list) {
        state.navList = list;
    },
    setSearchListData (state, list) {
        state.searchListData = list;
    }
};

const actions = {
    getTypeList ({ commit }, params) {
        publicPost(getApiUrl('getType'), params).then((res) => {
            if (params.type === 1) {
                if (config.lang === 'en') {
                    res.unshift({ name: 'all industries', code: '' });
                } else {
                    res.unshift({ name: '', code: '' });
                }
            }
            commit('setNavList', res);
        }).catch(() => {
            console.warn('error');
        });
    }
};

export default {
    state,
    actions,
    mutations
};
