import Vue from 'vue';
import VueRouter from 'vue-router';
import routes from './routes';
import CONFIG from '../config';
import store from '../store';

Vue.use(VueRouter);

const router = new VueRouter({
    // mode: 'hash', //  : "hash" | "history" | "abstract""hash"
    mode: 'history', //  : "hash" | "history" | "abstract""hash"
    // linkActiveClass: 'u-link--Active', // class
    // base: '/vuex-demo/', // '/'
    base: '/bbchain/web/', // ./config/index.jsbuild
    // scrollBehavior, //  // v-scroll-postion
    routes, // 
    scrollBehavior (to, from, saveTop) {
        if (saveTop) {
            return saveTop;
        } else {
            window.scrollTo(0, 1);
            return { x: 0, y: 0 };
        }
    }
});

router.beforeEach((to, from, next) => {
    store.commit('OPEN_LOADING');
    try {
        if (store.state) { // ""
            /**  */
            let key = from.query.timestamp,
                obj = Object.create(null);
            store.commit('SET_POSITION', {key: key, val: obj});
        }
    } catch (e) {
        console.error('router.beforeEach: ', e);
    }
    if (!store.getters.userList.length && !CONFIG.userList.length) {
        store.dispatch('signIn').then(res => {
            next();
        }).catch(e => {
            next();
        });
    } else {
        next();
    }
    // next();
});

// title
router.setTitle = function (title) {
    document.title = title;
    try {
        setTitle(title);    //eslint-disable-line
    } catch (e) {  }    //eslint-disable-line
};

router.afterEach(route => {
    /** set title */
    if (route.meta && route.meta.title) {
        router.setTitle(route.meta.title);
    } else {
        router.setTitle(CONFIG.title);
    }
    try { // ~
        if (store && store.state) {
            store.dispatch('CLOSE_LOADING');
        }
    } catch (e) {
        console.error('router.afterEach: ', e);
    }
});

router.direct = () => {
    if (store.getters.position.hasOwnProperty(router.currentRoute.query.timestamp)) {
        return 0; // 
    }
    return 1; // 
};

let p = router.push,
    r = router.replace;
router.push = function (params) {
    let tag = Date.now();
    if (params.query) {
        params.query.timestamp = tag;
    } else {
        params.query = {timestamp: tag};
    }
    p.call(router, params);
};
router.replace = function (params) {
    let tag = Date.now();
    if (params.query) {
        params.query.timestamp = tag;
    } else {
        params.query = {timestamp: tag};
    }
    r.call(router, params);
};

export default router;
