import config from '../../config';
import axios from 'axios';
import store from '../../store/';
import _promise from 'promise';

// Vue.use(axios);

if (typeof Promise === 'undefined') {
    var Promise = _promise;
}

// 
axios.interceptors.request.use(function (res) {
    // 
    res.headers = Object.assign({}, res.headers, {
        'lang': config.lang // header
    });
    return res;
}, function (error) {
    // 
    return Promise.reject(error);
});

// 
axios.interceptors.response.use(function (response) {
    // 
    return response;
}, function (error) {
    // 
    return Promise.reject(error);
});

export function post (uri, params, opts) {
    if (opts && opts.loading) {
        store.commit('OPEN_LOADING'); // 
    }
    let options = opts && opts.options || {}; // options of vue-resource
    options.params = opts && opts.query || {}; // queryAPI
    return axios.post(uri, params, {
        timeout: config.instance
    }).then(function (res) {
        if (opts && opts.loading) {
            store.dispatch('CLOSE_LOADING'); // 
        }
        var result = res.data;
        if (result.code === 200 || result.code === '200') {
            return Promise.resolve(result.data);
        } else {
            return Promise.reject(result.msg);
        }
    }, function (err) {
        if (opts && opts.loading) {
            store.dispatch('CLOSE_LOADING'); // 
        }
        return Promise.reject(err.msg);
    }).catch(function (err) {
        if (opts && opts.loading) {
            store.dispatch('CLOSE_LOADING'); // 
        }
        return Promise.reject(err.msg);
    });
};
export function get (uri, params, opts) {
    if (opts && opts.loading) {
        store.commit('OPEN_LOADING'); // 
    }
    return axios.get(uri, {params}, {
        timeout: config.instance
    }).then(function (res) {
        if (opts && opts.loading) {
            store.dispatch('CLOSE_LOADING'); // 
        }
        var result = res.data;
        if (result.code === 200 || result.code === '200') {
            return Promise.resolve(result.data);
        } else {
            return Promise.reject(result.msg);
        }
    }, function (err) {
        if (opts && opts.loading) {
            store.dispatch('CLOSE_LOADING'); // 
        }
        return Promise.reject(err.msg);
    }).catch(function (err) {
        if (opts && opts.loading) {
            store.dispatch('CLOSE_LOADING'); // 
        }
        return Promise.reject(err.msg);
    });
}
