import { publicGet } from '../../js/api/index.js';
import getApiUrl from '../../js/api/apiUrlList';
const state = {
    personalData: null, // 
    industryTypes: [],
    requestType: [],
    keywords: []
};

const mutations = {
    // 
    setPersonalData (state, obj) {
        state.personalData = obj;
    },
    setIndustry (state, list) {
        state.industryTypes = list;
    },
    setDemand (state, list) {
        state.requestType = list;
    },
    setKeywordList (state, list) {
        state.keywords = list;
    }
};

const actions = {
    getPersonalData ({ commit }, params) {
        publicGet(getApiUrl('getSub'), params).then((res) => {
            commit('setKeywordList', res.keywords);
            commit('setDemand', res.requestType);
            commit('setIndustry', res.industryTypes);
            commit('setPersonalData', res);
        }).catch(() => {
            console.warn('error');
        });
    }
};

export default {
    state,
    actions,
    mutations
};
